import React from 'react'
import { useHistory, useLocation } from 'react-router-dom'
import { Space, MenuItem } from '@looker/components'
import { ROUTES } from '../Geppetto'

export const Navigation = () => {
  const history = useHistory()
  const { pathname } = useLocation()

  const navigate = (route) => history.push(route)

  return (
    <Space py="small">
      <MenuItem
        onClick={navigate.bind(null, ROUTES.home)}
        selected={pathname === ROUTES.home}
      >
        Home
      </MenuItem>
      <MenuItem
        onClick={navigate.bind(null, ROUTES.changetitles)}
        selected={pathname === ROUTES.changetitles}
      >
        Change Titles
      </MenuItem>
      <MenuItem
        onClick={navigate.bind(null, ROUTES.hidetiles)}
        selected={pathname === ROUTES.hidetiles}
      >
        Hide Tiles
      </MenuItem>
      <MenuItem
        onClick={navigate.bind(null, ROUTES.selectviz)}
        selected={pathname === ROUTES.selectviz}
      >
        Select Viz
      </MenuItem>
      <MenuItem
        onClick={navigate.bind(null, ROUTES.selectlayout)}
        selected={pathname === ROUTES.selectlayout}
      >
        Select Layout
      </MenuItem>
    </Space>
  )
}
