import React, { Suspense } from 'react'
import { Switch, Route } from 'react-router-dom'
import { Spinner, ComponentsProvider, SpaceVertical } from '@looker/components'
import { EmbeddedDashboardProvider } from './providers/EmbeddedDashboardProvider'
import { Navigation } from './components/Navigation'

const HomeScene = React.lazy(() => import('./components/HomeScene'))
const ChangeTitlesScene = React.lazy(() =>
  import('./components/ChangeTitlesScene')
)
const HideTilesScene = React.lazy(() => import('./components/HideTilesScene'))
const SelectVizScene = React.lazy(() => import('./components/SelectVizScene'))
const SelectLayoutScene = React.lazy(() =>
  import('./components/SelectLayoutScene')
)

export const ROUTES = {
  home: '/',
  changetitles: '/changetitles',
  hidetiles: '/hidetiles',
  selectviz: '/selectviz',
  selectlayout: '/selectlayout',
}

export const Geppetto = () => (
  <ComponentsProvider>
    <EmbeddedDashboardProvider>
      <SpaceVertical p="small">
        <Navigation />
        <Suspense fallback={<Spinner />}>
          <Switch>
            <Route path={ROUTES.changetitles}>
              <ChangeTitlesScene />
            </Route>
            <Route path={ROUTES.hidetiles}>
              <HideTilesScene />
            </Route>
            <Route path={ROUTES.selectviz}>
              <SelectVizScene />
            </Route>
            <Route path={ROUTES.selectlayout}>
              <SelectLayoutScene />
            </Route>
            <Route>
              <HomeScene />
            </Route>
          </Switch>
        </Suspense>
      </SpaceVertical>
    </EmbeddedDashboardProvider>
  </ComponentsProvider>
)
