import React, { createContext, useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import cloneDeep from 'lodash/cloneDeep'
import merge from 'lodash/merge'
import { layoutSingleValues, layoutComponents } from './utils'

export const DashboardContext = createContext()

export const EmbeddedDashboardProvider = ({ children }) => {
  const [currentDashboardId, setCurrentDashboardId] = useState()
  const [originalOptions, setOriginalOptions] = useState()
  const [options, setOptions] = useState()
  const [tiles, setTiles] = useState([])

  useEffect(() => {
    if (options) {
      console.log('useEffect', { options })
      buildTiles(options)
    }
  }, [options])

  const buildTiles = (opts) => {
    const _options = cloneDeep(opts)
    console.log('buildTiles', { options: _options })
    const activeLayout = _options?.layouts.find(
      (layout) => layout.active === true
    )
    const _activeTiles = (activeLayout?.dashboard_layout_components || []).map(
      (tileLayout) => String(tileLayout.id)
    )
    const tileLayouts = {}
    ;(activeLayout?.dashboard_layout_components || []).forEach(
      (tileLayout) => (tileLayouts[tileLayout.id] = tileLayout)
    )
    const elements = Object.entries(_options?.elements || {})
    const tiles = elements.map(([key, element]) => ({
      id: String(key),
      ...element,
      hidden: !_activeTiles.includes(String(key)),
      layout: { ...(tileLayouts[key] || {}) },
    }))
    setTiles(tiles)
  }

  const saveOptions = (options) => {
    console.log('saveOptions', { options })
    setOriginalOptions(cloneDeep(options))
    setOptions(cloneDeep(options))
  }

  const restoreOptions = () => {
    console.log('restore options', { originalOptions })
    setOptions(cloneDeep(originalOptions))
  }

  const updateDashboardId = (dashboardId) => {
    setCurrentDashboardId(dashboardId)
    setTiles([])
    setOptions(undefined)
    setOriginalOptions(undefined)
  }

  const updateTile = (id, data) => {
    const tempOptions = cloneDeep(options)
    merge(tempOptions.elements[id], data)
    setOptions(tempOptions)
  }

  const updateTiles = (batchUpdates) => {
    const tempOptions = cloneDeep(options)
    batchUpdates.forEach(({ id, data }) => {
      merge(tempOptions.elements[id], data)
    })
    setOptions(tempOptions)
  }

  const updateActiveTiles = (_activeTiles) => {
    console.log('updateActiveTiles', { _activeTiles })
    const tempOptions = cloneDeep(options)
    const activeLayout = tempOptions?.layouts.find(
      (layout) => layout.active === true
    )
    const originalActiveLayout = originalOptions?.layouts.find(
      (layout) => layout.active === true
    )
    const dashboardLayoutComponents = []
    originalActiveLayout.dashboard_layout_components.forEach(
      (originalTileLayout) => {
        console.log({ originalLayout: originalTileLayout })
        if (_activeTiles.includes(String(originalTileLayout.id))) {
          const tileLayout = activeLayout.dashboard_layout_components.find(
            (_layout) => String(_layout.id) === String(originalTileLayout.id)
          )
          if (tileLayout) {
            dashboardLayoutComponents.push(tileLayout)
          } else {
            dashboardLayoutComponents.push(cloneDeep(originalTileLayout))
          }
        }
      }
    )
    activeLayout.dashboard_layout_components = dashboardLayoutComponents
    console.log('options after', { tempOptions })
    setOptions(tempOptions)
  }

  const updateLayout = (layoutType, compLayout) => {
    const tempOptions = cloneDeep(options)
    const activeLayoutIndex = tempOptions?.layouts.findIndex(
      (layout) => layout.active === true
    )
    const originalActiveLayout = originalOptions?.layouts.find(
      (layout) => layout.active === true
    )
    const tempActiveLayout = cloneDeep(originalActiveLayout)
    console.log('1 >>', cloneDeep(tempActiveLayout.dashboard_layout_components))
    tempActiveLayout.dashboard_layout_components.sort((a, b) => {
      if (a.row < b.row) return -1
      if (a.row > b.row) return 1
      if (a.row === b.row) {
        if (a.column < b.column) return -1
        if (a.column > b.column) return 1
      }
      return 0
    })
    console.log('2 >>', cloneDeep(tempActiveLayout.dashboard_layout_components))
    tempOptions.layouts[activeLayoutIndex] = tempActiveLayout
    if (layoutType === 'svl') {
      layoutSingleValues(
        tempActiveLayout.dashboard_layout_components,
        tempOptions.elements
      )
    } else if (layoutType === 'svr') {
      layoutSingleValues(
        tempActiveLayout.dashboard_layout_components,
        tempOptions.elements,
        {
          svColumn: 24 - 6,
          svRestColumn: 0,
        }
      )
    } else {
      layoutComponents(
        tempActiveLayout.dashboard_layout_components,
        tempOptions.elements,
        compLayout
      )
    }
    setOptions(tempOptions)
  }

  return (
    <DashboardContext.Provider
      value={{
        currentDashboardId,
        updateDashboardId,
        options,
        saveOptions,
        restoreOptions,
        tiles,
        updateTile,
        updateActiveTiles,
        updateTiles,
        updateLayout,
      }}
    >
      {children}
    </DashboardContext.Provider>
  )
}

EmbeddedDashboardProvider.propTypes = {
  children: PropTypes.object,
}
