import merge from 'lodash/merge'

const groupComponentsByType = (type, elements, components) => {
  const typeComponents = components
    .filter(
      ({ dashboard_element_id }) =>
        elements[dashboard_element_id].vis_config.type === type
    )
    .map(({ dashboard_element_id }) => dashboard_element_id)
  const rest = components
    .filter(
      ({ dashboard_element_id }) =>
        elements[dashboard_element_id].vis_config.type !== type
    )
    .map(({ dashboard_element_id }) => dashboard_element_id)
  return [typeComponents, rest]
}

export const layoutSingleValues = (components, elements, options = {}) => {
  const { svHeight, svWidth, totalRowWidth, svColumn, svRestColumn } = merge(
    {
      svHeight: 4,
      svWidth: 6,
      totalRowWidth: 24,
      svColumn: 0,
      svRestColumn: 24 - 6,
    },
    options
  )
  const restHeight = svHeight * 2
  const svRestWidth = totalRowWidth - svWidth
  const restColumn = totalRowWidth / 2
  const restWidth = restColumn

  const [singleValues, rest] = groupComponentsByType(
    'single_value',
    elements,
    components
  )

  const svMaxRow = svHeight * singleValues.length

  singleValues.forEach((el, i) => {
    const comp = components.find(
      ({ dashboard_element_id }) => dashboard_element_id === el
    )
    comp.row = i * svHeight
    comp.column = svColumn
    comp.width = svWidth
    comp.height = svHeight
  })

  let alternate = false
  rest.forEach((el, i) => {
    const comp = components.find(
      ({ dashboard_element_id }) => dashboard_element_id === el
    )
    if (i * restHeight < svMaxRow) {
      comp.row = i * restHeight
      comp.column = svRestColumn
      comp.width = svRestWidth
      comp.height = restHeight
    } else {
      alternate = !alternate
      comp.row = i * restHeight
      comp.width = restWidth
      comp.height = restHeight
      if (alternate) {
        comp.column = 0
      } else {
        comp.column = restColumn
      }
    }
  })
}

export const layoutComponents = (components, elements, compLayouts) => {
  compLayouts.forEach(({ id, title, row, column, width, height }) => {
    const compId =
      id || Object.keys(elements).find((key) => title === elements[key].title)
    const comp = components.find((comp) => compId === comp.dashboard_element_id)
    comp.column = column
    comp.row = row
    comp.width = width
    comp.height = height
  })
}
